body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #3975FF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

#quote-box {
  position: relative;
  background-color: #85A8F9;
  margin-top: 20vh;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 3px 3px 6px #0435A5;
  height: 400px;
}

#tweet-quote {
  margin-left: 20px;
  text-shadow: 1px 1px 2px grey;
}

.button-box {
  position: absolute;
  bottom: 0;
  right: 0;
  padding-bottom: 20px;
  padding-right: 20px;
  display:flex;
  justify-content:flex-end;
  align-items:center;
}
